.login{
    display: flex;
   
    .loginContainer{
       flex: 6;
   
       .top, .middle{
         -webkit-box-shadow: 2px 4px 10px 1px rgba(0,0,0,0.47);
         box-shadow: 2px 4px 10px 1px rgba(201,201, 201, 0.47);
         padding: 10px;
         margin: 10px;
         gap: 20px;
         color: gray;
        
         .title{
           font-weight: 500;
           color:gray;
           font-size: 45px;
           text-align: center;
           align-items: center;
           margin-bottom: 10px;
          }
       }
   
       .middle{
         display: flex;
         padding: 5px 20px;
         .right{
           flex: 3;
           -webkit-box-shadow: 2px 4px 10px 1px rgba(0,0,0,0.47);
         box-shadow: 2px 4px 10px 1px rgba(201,201, 201, 0.47);
         padding: 20px;
         margin: 20px;
         .adDesc{
          font-weight: 500;
           color:gray;
           font-size: 25px;
           align-items: center;
           text-align: justify;
           margin-top: 50px;
         }
         }
   
         .left{
           flex: 3;
           -webkit-box-shadow: 2px 4px 10px 1px rgba(0,0,0,0.47);
         box-shadow: 2px 4px 10px 1px rgba(201,201, 201, 0.47);
         padding: 20px;
         margin: 20px;
         gap: 20px;

         .title{
            text-align: center;
            color:rgb(39, 187, 192);
            margin-bottom: 25px;
            font-size: 34px;
           }

         .details{
            align-items: center;
            gap: 20px;
            padding: 20px;

            .detailItem{
                margin-bottom: 10px;
                font-size: 14px;

                .itemKey{
                    font-weight: bold;
                    color: gray;
                    margin: 5px;

                    .icon{
                        cursor: pointer;
                    }
                }

                .itemValue{
                    font-weight: 300;

                    input{
                        width: 100%;
                        padding: 10px;
                        border: none;
                        border-radius: 5px;
                    }
                }
            }

            .button{
                text-align: center;

                button{
                    width: 200px;
                    padding: 10px;
                    border: none;
                    background-color: teal;
                    color: white;
                    font-weight: bold;
                    cursor: pointer;
                    margin-top: 10px;
                }
            }
        }
         }
       }
   
       .bottom{
         -webkit-box-shadow: 2px 4px 10px 1px rgba(0,0,0,0.47);
         box-shadow: 2px 4px 10px 1px rgba(201,201, 201, 0.47);
         padding: 20px;
   
        .title{
         text-align: center;
         align-items: center;
         color:gray;
         margin-bottom: 15px;
        }
       }
   
    }
   }